.chapter{
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 1;
}
.front-chapter{
    z-index: 10;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.20);
    .chapter-head{
        background: rgba(11, 148, 161, 0.50);
        font-size: 40px;
        font-family: Metropolis-Light;
        text-align: center;
        margin: 40px 20px 20px 20px;
        padding: 10px;
        color: #FFF;
        h4{
            color: #FFF;
        }
    }
    .sealted-list-inner{
        margin: 20px;
        .list-bullet{
            background: #13638a;
            border: none;
        }
        .list-item-wrapper{
            margin-top: 12px;
            padding-top: 0;
        }
    }
    .actions{
        margin: 20px;
    }
}