.sealted-list{
    .list-wrapper {
        position:relative;
      }
      .list-item-wrapper {
        padding-top: 20px;
        position:relative;
      }
      .list-bullet {
        float: left;
        margin-right: 16px;
        border: 1px solid #e8e8e8;
        height: 30px;
        width: 30px;
        line-height: 30px;
        border-radius: 100px;
        font-weight: 700;
        color: white;
        text-align: center;
        background: #124556;
        font-family: Metropolis-Regular;
        font-size: 12px;
      }
      .list-item {
        display:table-row;
        vertical-align:middle;
        
      }
      .list-title {
          font-weight:700;
          color: #fff;
          font-family: Metropolis-Light;
          font-size: 16px;
      }
      .list-text {
          font-weight:400;
          
      }
      .red-line {
        background: #fff;
        width: 1px;
        height: 100%;
        position: absolute;
        left: 15px;
        z-index: 0;
      }
}