.content{
    background: url('./chapter.jpg');
    min-height: 100vh;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    .actions{
        padding: 20px;
    }
    .content-head{
        margin: 20px;
        h5{
            color: #FFF;
            font-family: Metropolis-Medium;
            font-size: 20px;
            float: left;
        }
    }
    .p-cont{
        padding-top: 51px;
        padding-left: 20px;
        padding-right: 20px;
        color: #FFF;
        p{
            font-family: Metropolis-Light;
            font-size: 14px;
            color: rgba(255, 255, 255, 0.77);
        }
    }
}