.sealted-list-inner{
    .list-wrapper {
        position:relative;
      }
      .list-item-wrapper {
        padding-top: 20px;
        position:relative;
      }
      .list-bullet {
        float: left;
        margin-right: 16px;
        border: 1px solid rgba(255, 255, 255, 0.76);
        height: 26px;
        width: 26px;
        line-height: 26px;
        border-radius: 100px;
        font-weight: 700;
        color: rgba(255, 255, 255, 0.76);
        text-align: center;
        background: #124556;
        font-family: Metropolis-ExtrsLight;
        font-size: 12px;
      }
      .list-item {
        display:table-row;
        vertical-align:middle;
        
      }
      .list-title {
          font-weight:700;
          color: rgba(255, 255, 255, 0.76);
          font-family: Metropolis-ExtrsLight;
          font-size: 14px;
          margin-top: 3px;
      }
      .list-text {
          font-weight:400;
          
      }
      .red-line {
        background: rgba(255, 255, 255, 0.76);
        width: 1px;
        height: 100%;
        position: absolute;
        left: 14px;
        z-index: 0;
      }
}