.splash{
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 1;
}
.front{
    z-index: 10;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.20);
    .centered{
        text-align: center;
        color: #FFF;
        .cen-text{
            width: 100%
        }
        h4{
            font-family: Snell Roundhand;
            color: #FFF;
            width: 100%;
            font-size: 40px;
        }
        .sub-head{
            font-size: 10px;
            width: 100%;
        }
    }
    .btn{
        color: #FFF !important;
        border-color: #FFF !important;
        margin: 20px;
        font-family: Metropolis-Regular;
        line-height: 24px;
    }
}